import React from "react";
import Message from "./Message";
import { withRouter } from "react-router-dom";

export default withRouter(function RouteMessage(props: any) {
    var params: any = new URLSearchParams(props.location.search);
    return (<Message title={params.get("a")} message={params.get("b")} onClose={x => props.history.goBack()} />)
});